import React from 'react'
import './App.css'
import Right from './components/Right'
import Topbar from './components/Topbar'
import Title from './components/Title'
import Head from './components/Head'
import Loading from './components/Loading'
import Body from './components/Body'
import Error from './components/Error'
import Footer from './components/Footer'

class App extends React.Component {
  constructor(props) {
    super(props)
    // Ìï®Ïàò ÎßÅÌÅ¨
    this.handleChange = this.handleChange.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
    this.handleButton = this.handleButton.bind(this)
    this.handleClick = this.handleClick.bind(this)
    // ÌôòÍ≤Ω Ïä§ÌÖåÏù¥Ìä∏ ÏÑ§Ï†ï
    this.state = {
      inputText: '',
      name: '',
      profileImg: null,
      profileDesc: {},
      userResult: {},
      isShow: false,
      isLoading: false,
      rowToShow: 5,
      isError: false,
      statusCode: null,
      history: null
    }
  }
  // ÌûàÏä§ÌÜ†Î¶¨Ïö© ÎπÑÎèôÍ∏∞ ÎßàÏö¥ÌåÖ
  componentDidMount() {
    // Right history asynchronous update
    const setState = this.setState.bind(this)
    fetch('https://cors-anywhere.herokuapp.com/https://terada.herokuapp.com/', {
      method: 'GET',
      headers: { 'content-type': 'application/json' }
    }).then(res => res.json())
      .then(function (res) {
        setState({
          history: res
        })
      })
  }
  // ÏûÖÎ†•Ìèº Ìï∏Îì§
  handleChange(e) {
    this.setState({
      inputText: e.target.value
    })
  }
  // ÌûàÏä§ÌÜ†Î¶¨ ÌÅ¥Î¶≠ - GET
  handleClick(e) {
    e.preventDefault()
    const name = e.target[Object.keys(e.target)[1]].children
    if (this.state.name !== name) {
      this.setState({
        name: name,
        isLoading: true
      })
      fetch(`https://cors-anywhere.herokuapp.com/https://terada.herokuapp.com?q=${encodeURIComponent(JSON.stringify({ data: name }))}`)
        .then(response => response.json())
        .catch(() => {
          console.log('Error!')
          this.setState({
            isLoading: false,
            isError: true,
            isShow: true
          })
          throw Error
          // ÏΩîÎìú Ïã§Ìñâ Ï§ëÏßÄ .catch Î∂àÏôÑÏ†Ñ
        })
        .then(response => {
          if (response.data.statusCode === 200) {
            this.setState({
              statusCode: 200,
              profileImg: response.data.userInfo.profileImg,
              profileDesc: {
                name: response.data.userInfo.profileDesc.name,
                screen_name: response.data.userInfo.profileDesc.screen_name,
                description: response.data.userInfo.profileDesc.description,
                follower: response.data.userInfo.profileDesc.follower,
                following: response.data.userInfo.profileDesc.following,
                isVerified: response.data.userInfo.profileDesc.verified
              },
              userResult: {
                userResultImage: response.data.userResult.userResultImage,
                userResultVideo: response.data.userResult.userResultVideo,
              },
              isShow: true,
              isLoading: false,
              isError: false
            })
          }
          else if (response.data.statusCode === 401) {
            // 401 Ïù∏Ï¶ù Ïò§Î•ò => ÎπÑÎ∞Ä Í≥ÑÏ†ï
            this.setState({
              statusCode: 401,
              isShow: true,
              isLoading: false
            })
          }
          else if (response.data.statusCode === 404) {
            // 404 ÌéòÏù¥ÏßÄ Ï°¥Ïû¨ ÏïàÌï® => Ï°¥Ïû¨ÌïòÏßÄ ÏïäÎäî ÏÇ¨Ïö©Ïûê    
            this.setState({
              statusCode: 404,
              isShow: true,
              isLoading: false
            })
          }
          else if (response.data.statusCode === 999) {
            // 999 => Ï°¥Ïû¨ÌïòÎäî ÏÇ¨Ïö©Ïûê, Ìä∏ÏúóÏùÑ ÌïòÏßÄ ÏïäÏùå
            this.setState({
              statusCode: 999,
              isShow: true,
              isLoading: false
            })
          }
        })
    }
  }
  // Ìèº ÏûÖÎ†• - POST
  handleSubmit(e) {
    e.preventDefault()
    if (this.state.name !== this.state.inputText) {
      this.setState({
        name: this.state.inputText,
        isLoading: true
      })
      fetch('https://cors-anywhere.herokuapp.com/https://terada.herokuapp.com/', {
        // Heroku supports cors proxy server, Holy shit!
        method: 'POST',
        headers: {
          'content-type': 'application/json'
        },
        body: JSON.stringify({ data: this.state.inputText })
        // setState lag ÎïåÎ¨∏Ïóê inputTextÎ°ú Î≥¥ÎÉÑ

      }).then(response => response.json())
        .catch(() => {
          console.log('Error!')
          this.setState({
            isLoading: false,
            isError: true,
            isShow: true
          })
          throw Error
          // ÏΩîÎìú Ïã§Ìñâ Ï§ëÏßÄ .catch Î∂àÏôÑÏ†Ñ
        })
        .then(response => {
          if (response.data.statusCode === 200) {
            this.setState({
              statusCode: 200,
              profileImg: response.data.userInfo.profileImg,
              profileDesc: {
                name: response.data.userInfo.profileDesc.name,
                screen_name: response.data.userInfo.profileDesc.screen_name,
                description: response.data.userInfo.profileDesc.description,
                follower: response.data.userInfo.profileDesc.follower,
                following: response.data.userInfo.profileDesc.following,
                isVerified: response.data.userInfo.profileDesc.verified
              },
              userResult: {
                userResultImage: response.data.userResult.userResultImage,
                userResultVideo: response.data.userResult.userResultVideo,
              },
              isShow: true,
              isLoading: false,
              isError: false
            })
          }
          else if (response.data.statusCode === 401) {
            // 401 Ïù∏Ï¶ù Ïò§Î•ò => ÎπÑÎ∞Ä Í≥ÑÏ†ï
            this.setState({
              statusCode: 401,
              isShow: true,
              isLoading: false
            })
          }
          else if (response.data.statusCode === 404) {
            // 404 ÌéòÏù¥ÏßÄ Ï°¥Ïû¨ ÏïàÌï® => Ï°¥Ïû¨ÌïòÏßÄ ÏïäÎäî ÏÇ¨Ïö©Ïûê    
            this.setState({
              statusCode: 404,
              isShow: true,
              isLoading: false
            })
          }
          else if (response.data.statusCode === 999) {
            // 999 => Ï°¥Ïû¨ÌïòÎäî ÏÇ¨Ïö©Ïûê, Ìä∏ÏúóÏùÑ ÌïòÏßÄ ÏïäÏùå
            this.setState({
              statusCode: 999,
              isShow: true,
              isLoading: false
            })
          }
        })

    }
  }
  // ReadMore
  handleButton() {
    this.setState({
      rowToShow: this.state.rowToShow + 5
    }
    )
  }

  render() {
    // Default
    if (!this.state.isShow) {
      return (
        <div className='wrapper-main'>
          <div className="App">
            < Title />
            < Head onChange={this.handleChange} onSubmit={this.handleSubmit} />
            {this.state.isLoading ? <Loading /> : null}
          </div>
          < Right handleClick={this.handleClick} history={this.state.history} />
        </div>
      )
    }
    // Search 
    else {
      if (this.state.statusCode === 200) {
        const data = {
          profileImg: this.state.profileImg,
          profileDesc: this.state.profileDesc,
          userResult: this.state.userResult,
          rowToShow: this.state.rowToShow,
          isVerified: this.state.isVerified
        }
        return (
          <div className='wrapper-main'>
            <div className="App">
              < Topbar />
              < Title />
              < Head onChange={this.handleChange} onSubmit={this.handleSubmit} />
              {this.state.isLoading ? < Loading /> : null}
              < Body data={data} onButton={this.handleButton} />
              < Footer />
            </div>
            < Right handleClick={this.handleClick} history={this.state.history} />
          </div>
        )
      }
      else {
        return (
          <div className='wrapper-main'>
            <div className="App">
              < Title />
              < Head onChange={this.handleChange} onSubmit={this.handleSubmit} />
              {this.state.isLoading ? < Loading /> : null}
              < Error statusCode={this.state.statusCode} name={this.state.name} />
            </div>
            < Right handleClick={this.handleClick} history={this.state.history} />
          </div>
        )
      }
    }
  }
}

export default App
